import "./firebase";
import {
  getFirestore,
  collection,
  addDoc,
  getDocs,
  doc,
} from "firebase/firestore";
import { query, where } from "firebase/firestore";

import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { app } from "./firebase";

const db = getFirestore();
const petCollection = collection(db, "pets");

export default db;

export function createPet() {
  const ref = doc(petCollection);

  addDoc(collection(db, "pets"), {
    id: ref.id,
    name: this.name,
    image_url: this.image_url,
    location: this.location,
    species: this.species,
    sex: this.sex,
    size: this.size,
    birth_date: this.birth_date,
    handle_status: this.handle_status,
    bio: this.bio,
    status: this.status,
    personality: this.personality,
    info_notes: this.info_notes,
  }).then(function (docRef) {
    console.log("Document written with ID: ", docRef.id);
  });
}

export async function getAllPets() {
  const petsInfo = [];

  const querySnapshot = await getDocs(collection(db, "pets"));
  querySnapshot.forEach((doc) => {
    petsInfo.push(doc.data());
  });
  return petsInfo;
}

export function uploadPicture(file) {
  const storage = getStorage(app);
  const storageRef = ref(storage, file.name);
  console.log(storageRef, file);

  uploadBytes(storageRef, file).then((snapshot) => {
    console.log("Uploaded!");
    console.log("File metadata:", snapshot.metadata);

    getDownloadURL(snapshot.ref).then((url) => {
      console.log("File available at", url);
      this.image_url = url;
      this.createPet();
    });
  });
}

export async function search(q) {
  let petArray = [];
  console.log(q);

  if (q == "") {
    petArray = await getAllPets();
  } else {
    const petsRef = collection(db, "pets");
    console.log(petsRef);
    const resultado = query(petsRef, where("name", "==", q));
    const querySnapshot = await getDocs(resultado);
    console.log(querySnapshot);
    querySnapshot.forEach((doc) => {
      console.log(doc.id, " => ", doc.data());
      petArray.push(doc.data());
    });
  }
  return petArray;
}
