<script>
import { createPet, uploadPicture } from "../db";
export default {
  name: "NewPet",
  data() {
    return {
      name: "",
      image_url: "",
      my_file: "",
      file_url: "",
      location: "",
      species: "",
      sex: "",
      size: "",
      birth_date: "",
      handle_status: {
        vaccinated: null,
        dewormed: null,
        sterilized: null,
        microchip: null,
      },
      bio: "",
      status: "",
      personality: [],
      info_notes: [],
    };
  },
  methods: {
    createPet,
    uploadPicture,
    uploadPhoto(event) {
      this.my_file = event.target.files[0];
      this.file_url = URL.createObjectURL(this.my_file);
      console.log(URL.createObjectURL(this.my_file));
    },
    errorCheck() {
      if (
        this.name != "" &&
        this.location != "" &&
        this.species != "" &&
        this.sex != "" &&
        this.size != "" &&
        this.birth_date != "" &&
        this.bio != "" &&
        this.handle_status.vaccinated != null &&
        this.handle_status.dewormed != null &&
        this.handle_status.sterilized != null &&
        this.handle_status.microchip != null &&
        this.my_file != ""
      ) {
        console.log("No errors detected!");
        this.uploadPicture(this.my_file);
        //this.createPet();
      } else {
        console.log("error");
        window.alert(
          "Falta uno de los campos requeridos. Por favor, revisa la información entrada."
        );
      }
    },
    addOption(event) {
      console.log(event.srcElement.value);
      if (event.key == "Enter") {
        this.info_notes.push(event.srcElement.value);
        event.srcElement.value = "";
        console.log(this.info_notes.length);

        var newOption = document.createElement("div");
        newOption.innerHTML = this.info_notes[this.info_notes.length - 1];
        newOption.style.padding = "0.3em";
        newOption.style.marginLeft = "0.3em";
        newOption.style.background = "greenyellow";
        newOption.style.borderRadius = "10px";
        newOption.style.fontWeight = "bold";
        newOption.style.cursor = "pointer";

        newOption.addEventListener("click", function () {
          //var value = event.target.innerHTML;
          console.log(this);
          this.remove();
        });

        document.getElementById("info_notes_div").appendChild(newOption);
      }
    },
  },
};
</script>

